package de.kampfkalender.common

import kotlinx.serialization.Serializable

@Serializable
@Suppress("unused")
enum class Broadcaster(
    val title: String,
    val old: String? = null,
) {
    ARD(
        title = "ARD"
    ),
    BELLATOR_APP(
        title = "Bellator App"
    ),
    BILD_PLUS(
        title = "Bild+"
    ),
    BRAVECF_TV(
        title = "bravecftv.com"
    ),
    CBS_SPORTS(
        title = "CBS Sports"
    ),
    DAZN(
        title = "DAZN"
    ),
    EUROSPORT(
        title = "Eurosport"
    ),
    EUROSPORT2(
        title = "Eurosport 2"
    ),
    FACEBOOK(
        title = "Facebook"
    ),
    FIGHT24_TV(
        title = "fight24.tv"
    ),
    FIGHTING_DE(
        title = "fighting.de"
    ),
    FITE_TV(
        title = "fite.tv"
    ),
    GLORY_TV(
        title = "glory.tv"
    ),
    GMC_TV(
        title = "gmc.tv"
    ),
    IMMAF_TV(
        title = "immaf.tv"
    ),
    KSW_TV(
        title = "kswtv.com"
    ),
    MDR(
        title = "MDR"
    ),
    MEINSPORTPODCAST_DE(
        title = "meinsportpodcast.de"
    ),
    OKTAGON_TV(
        title = "oktagon.tv"
    ),
    ONE_APP(
        title = "ONE Super App"
    ),
    PRO7_MAXX(
        title = "ProSieben MAXX"
    ),
    RAN_FIGHTING(
        title = "ranfighting.de",
        old = "ranFighting"
    ),
    SPORT1(
        title = "SPORT1"
    ),
    SPORTSCHAU_DE(
        title = "sportschau.de"
    ),
    UFC_FIGHT_PASS(
        title = "UFC Fight Pass"
    ),
    YOUTUBE(
        title = "YouTube"
    ),
    XYZSPORTS_TV(
        title = "xyzsports.tv"
    ),
    ZDF(
        title = "ZDF"
    );

    companion object {
        fun parse(value: String): Broadcaster? {
            return values().find {
                it.name.equals(value, true)
                        || it.title.equals(value, true)
                        || it.old?.equals(value, true) == true
            }
        }
    }
}
