import kotlin.js.Promise

private var EVENTS: List<ExportedEvent> = listOf()

private fun loadEvents(i: DateInterval): Promise<List<ExportedEvent>> {
    return Promise.all(
        // Build list of requests.
        buildList {
            add("/data/${i.start.getUTCFullYear()}/${dateDigit(i.start.getUTCMonth() + 1)}.json")
            if (i.start.getUTCMonth() != i.end.getUTCMonth()) {
                add("/data/${i.end.getUTCFullYear()}/${dateDigit(i.end.getUTCMonth() + 1)}.json")
            }
        }
            // Execute each request and return a promise.
            .map { url: String ->
                window.fetch(url)
                    .then {
                        it.text()
                    }
                    .then {
                        Json.decodeFromString<List<ExportedEvent>>(it)
                    }
                    .catch {
                        listOf<ExportedEvent>()
                    }
            }.toTypedArray()
    )
        // Merge request results into a single list.
        .then {
            buildList {
                it.forEach {
                    addAll(it)
                }
            }
                .filter {
                    val interval = jso<DateInterval> {
                        start = it.time.toDate()
                        end = it.until.toDate()
                    }
                    areIntervalsOverlapping(i, interval)
                }
                //.sortedWith(ExportedEvent.DEFAULT_SORT)
                .sorted()
        }
}

private fun getInterval(currentDate: Date): DateInterval {
    return jso {
        start = currentDate
        end = addDays(currentDate, 14)
    }
}

val IndexPage = FC<Props> {
    val isSmall = useMediaQuery("(max-width:600px)")
    val (currentDate, setCurrentDate) = useState(Date())
    val (currentFilter, setCurrentFilter) = useState(FILTER_DEFAULT)
    val (events, setEvents) = useState(listOf<ExportedEvent>())

    val filterEvents = {
        setEvents(EVENTS.filter { e: ExportedEvent ->
            if (currentFilter.promotions.isNotEmpty()) {
                if (!currentFilter.promotions.contains(e.promotion)) {
                    return@filter false
                }
            }
            if (currentFilter.sports.isNotEmpty()) {
                if (!currentFilter.sports.any { e.sports.contains(it) }) {
                    return@filter false
                }
            }
            if (currentFilter.broadcasters.isNotEmpty()) {
                if (!currentFilter.broadcasters.any { e.broadcasters.contains(it) }) {
                    return@filter false
                }
            }
            return@filter true
        })
    }

    // Current date was changed.
    useEffect(currentDate) {
        loadEvents(getInterval(currentDate))
            .then {
                EVENTS = it
                filterEvents()
            }
    }

    // Current filter was changed.
    useEffect(currentFilter) {
        filterEvents()
    }

    Box {
        sx = jso {
            display = Display.flex
            paddingTop = 1.em

            if (isSmall) {
                flexDirection = FlexDirection.column
            } else {
                flexDirection = FlexDirection.row
            }
        }

        //Tooltip {
        //    title = ReactNode("Ab diesem Datum werden dir alle Events der folgenden 14 Tage angezeigt.")
        TextField {
            sx = jso {
                if (isSmall) {
                    marginBottom = 1.em
                } else {
                    marginRight = 0.5.em
                    flexGrow = FlexGrow(0.0)
                }
            }

            size = if (isSmall) {
                Size.small
            } else {
                Size.medium
            }
            // fullWidth = true
            required = true
            type = InputType.date
            defaultValue = currentDate.toIsoDate()
            onInput = {
                // println("onChange ${value} => ${date}")
                val value = it.asDynamic().target.value as String
                val date = parseISO(value)
                setCurrentDate(date)
            }
            label = ReactNode("Events ab")
            InputLabelProps = jso {
                required = false
            }
        }
        //}

        FilterSettings {
            filter = currentFilter
            setFilter = { setCurrentFilter(it) }
        }
    }

    Divider {
        sx = jso {
            marginTop = 1.em
            marginBottom = 1.em
        }
    }

    buildMap {
        events.forEach {
            val startDay = startOfDay(it.time.toDate())
            val endDay = startOfDay(it.until.toDate())
            val daysBetween = differenceInDays(startDay, endDay)

            for (i in 0..daysBetween) {
                val day = addDays(startDay, i)
                val key = day.toIsoDate()
                val e = if (daysBetween == 0) {
                    it
                } else {
                    it.copy(
                        time = day.toDateTime(),
                        until = if (isSameDay(day, it.until.toDate())) {
                            it.until
                        } else {
                            endOfDay(day).toDateTime()
                        }
                    )
                }
                this@buildMap.put(
                    key,
                    this@buildMap.getOrElse(key) { listOf<ExportedEvent>() }.plus(e)
                )
            }
        }
    }.forEach {
        Schedule {
            this.day = startOfDay(it.value.first().time.toDate())
            this.events = it.value
        }
    }
}
