package de.kampfkalender.web.components

import csstype.AlignItems
import csstype.Display
import csstype.FlexDirection
import csstype.FlexGrow
import csstype.FontWeight
import csstype.TextAlign
import csstype.UserSelect
import csstype.px
import csstype.rem
import csstype.rgba
import de.kampfkalender.common.ExportedEvent
import de.kampfkalender.web.ApplicationContext
import de.kampfkalender.web.date.format
import de.kampfkalender.web.date.locale.DE
import kotlinext.js.jso
import mui.material.Box
import mui.material.Typography
import mui.material.useMediaQuery
import react.FC
import react.Props
import react.dom.html.ReactHTML
import react.useContext
import kotlin.js.Date

external interface ScheduleProps : Props {
    var day: Date
    var events: List<ExportedEvent>
}

val Schedule = FC<ScheduleProps> { props ->
    val app = useContext(ApplicationContext)
    // val theme = useTheme<Theme>()
    val isSmall = useMediaQuery("(max-width:600px)")

    val calendarColor = if (app.darkMode)
        rgba(255, 255, 255, 0.5)
    else
        rgba(0, 0, 0, 0.5)

    Box {
        component = ReactHTML.div
        sx = jso {
            display = Display.flex

            if (isSmall) {
                flexDirection = FlexDirection.column
                marginBottom = 1.rem
            } else {
                flexDirection = FlexDirection.row
            }
        }

        Box {
            sx = jso {
                display = Display.flex
                textAlign = TextAlign.center
                userSelect = UserSelect.none

                if (isSmall) {
                    alignItems = AlignItems.baseline
                    flexDirection = FlexDirection.row
                    marginBottom = 1.rem
                } else {
                    flexGrow = FlexGrow(0.0)
                    flexDirection = FlexDirection.column
                    width = 60.px
                    marginRight = 1.5.rem
                }
            }

            // CalendarToday {
            //     sx = jso {
            //         fontSize = 5.rem
            //     }
            //     // fontSize = SvgIconSize.large
            // }

            Typography {
                sx = jso {
                    color = calendarColor
                    if (isSmall) {
                        fontWeight = FontWeight.bolder
                    } else {
                        fontWeight = FontWeight.bolder
                        letterSpacing = 3.px
                    }
                }
                component = ReactHTML.h4
                variant = "h4"

                +format(props.day, "EE", jso { locale = DE }).substringBeforeLast(".").trim()
            }
            Typography {
                sx = jso {
                    color = calendarColor
                    if (isSmall) {
                        fontWeight = FontWeight.lighter
                        marginLeft = 0.7.rem
                        marginRight = 0.6.rem
                    }
                }
                if (isSmall) {
                    component = ReactHTML.h4
                    variant = "h4"
                }

                +format(props.day, "d LLL", jso { locale = DE }).trim()
            }
            Typography {
                sx = jso {
                    color = calendarColor
                    if (isSmall) {
                        fontWeight = FontWeight.lighter
                    }
                }
                if (isSmall) {
                    component = ReactHTML.h4
                    variant = "h4"
                }

                +format(props.day, "yyyy", jso { locale = DE }).trim()
            }
        }

        Box {
            sx = jso {
                flexGrow = FlexGrow(1.0)
            }

            props.events.forEach {
                ScheduleEntry {
                    event = it
                }
            }
        }
    }
}
