package de.kampfkalender.web.components

import csstype.em
import de.kampfkalender.common.ExportedEvent
import de.kampfkalender.web.date.format
import de.kampfkalender.web.toDate
import kotlinext.js.jso
import mui.material.Card
import mui.material.CardHeader
import react.FC
import react.Props
import react.ReactNode

external interface ScheduleEntryProps : Props {
    var event: ExportedEvent
}

val ScheduleEntry = FC<ScheduleEntryProps> { props ->

    val timeRange = format(props.event.time.toDate(), "HH:mm") + " bis " +
            format(props.event.until.toDate(), "HH:mm") + " Uhr"

    Card {
        sx = jso {
            marginBottom = 1.5.em
        }

        CardHeader {
            title = ReactNode(props.event.title)
            subheader = ReactNode(timeRange)
        }
    }

    // ListItem {
    //   Typography {
    //       component = ReactHTML.div
    //       variant = "h6"
    //
    //       +props.event.title
    //       // +formatISO(props.event.time.toDate())
    //   }
    // }
}
